body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.App-logo {
  height: 200px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.header {
    width: 100%;
    height: 100px;
    background-color: rgb(191, 173, 136);
    margin-bottom: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.header img{
    height: 60px;
}

.header-title {
    font-family: 'Noto Sans', Roboto;
    font-weight: 500;
    font-size: 30px;
}
.indicator {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    /* justify-content: center; */
    -webkit-align-items: center;
            align-items: center
}

.indicator img{
    width: 250px;
    height: 250px;
    margin: 5px;
}

.indicator-text {
    margin: 50px;
    font-size: 30px;
}
.table {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    width: 800px;
}
.table .ReactTable{
    width:100%;
}
.smell-indicator {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 20px;
    margin-right: 30px;
    width: auto;
    height: auto;
    display: -webkit-flex;
    display: flex;
}

.smell-indicator-text{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: white;

}

.smell-indicator-value {
    font-size: 40px;
}
.smell-indicator-icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 30px;
    background-color: #5d606d;
    overflow: hidden;
    border: solid white;
}
.smell-indicator-icon img{
    width: 50px;
    height: 50px;
}

.green {
    color: green;
}
.yellow {
    color: yellow;
}
.orange {
    color: orange;
}
.red {
    color: red;
}

